.mainheader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 2em;
}

.filters {
    display: flex;
    justify-content: center;
    margin-top: 1em;
}

@media only screen and (max-width: 1026px) {
    .mainheader {
        display: block;
    }

    #hide {
        max-width: 50%;
    }
}

